function openSideMenu() {
    $(".side-overlay").css('display', 'block');

    $('.side-nav').animate({
            right: 0,
        }, 100,
        function() {
            // Animation complete.
            $('#main-container').animate({
                marginLeft: 250
            }, 100)
            $('#sidebar').animate({
                width: 250
            }, 100);
        });
    $(this).parent().find(".side-popup").fadeIn("fast");
}

function closeSideMenu() {
    $('.side-fltr').find(".side-popup").fadeOut("fast");
    $('.side-overlay').fadeOut("fast");

    $('#main-container').animate({
        marginLeft: 75
    })
    $('#sidebar').animate({
        width: 75
    }, 100);
}
